import React from "react";
import axios from "axios";

class PersonList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      dni: "",
      user: null,
    };
  }

  handleChange = (event) => {
    this.setState({ dni: event.target.value });
  };

  handleSubmit = (event) => {
    event.preventDefault();

    const user = {
      dni: this.state.dni,
    };
    var link = "https://dni.optimizeperu.com/api/persons/" + this.state.dni;
    console.log("link", link);
    axios.get(link, null).then((res) => {
      console.log(res);
      console.log(res.data);
      this.setState({
        user: res.data,
      });
    });
  };

  render() {
    return (
      <div className="App">
        <header className="App-header">
          <form onSubmit={this.handleSubmit}>
            <label>
              Person Name:
              <input type="text" name="name" onChange={this.handleChange} />
            </label>
            <button type="submit">Add</button>
          </form>
          {this.state.user != null ? (
            <div>
              DATOS OBTENIDOS: <br></br>
              <label>
                DNI:
                <input type="text" name="name" value={this.state.user.dni} />
              </label>
              <label>
                Nombres:
                <input type="text" name="name" value={this.state.user.name} />
              </label>
              <label>
                Apellidos:
                <input
                  type="text"
                  name="name"
                  value={
                    this.state.user.first_name + " " + this.state.user.last_name
                  }
                />
              </label>
            </div>
          ) : null}
        </header>
      </div>
    );
  }
}
export default PersonList;
